













































.mh-picker-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.mh-select-options-title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  height: 45px;
  line-height: 45px;
  font-size: 18px;
  font-weight: 400;
  border-bottom: 1px solid #e5e5e5;
  background-color: #f8f8f8;
}
.mh-selece-options-cancle {
  padding: 0 15px;
  color: #c1c1c1;
}
.mh-selece-options-comfirm {
  padding: 0 15px;
  color: #0c8ae0;
}

.mh-picker {
  display: block;
  position: relative;
  height: 270px;
  overflow: hidden;
  width: 100%;
}
.mh-picker .mh-picker__mask {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    margin: 0 auto;
    width: 100%;
    z-index: 3;
    -webkit-transform: translateZ(0px);
            transform: translateZ(0px);
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.9)), to(rgba(255, 255, 255, 0.6))), -webkit-gradient(linear, left bottom, left top, from(rgba(255, 255, 255, 0.9)), to(rgba(255, 255, 255, 0.6)));
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.6)), linear-gradient(to top, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.6));
    background-position: top, bottom;
    background-size: 100% 108px;
    background-repeat: no-repeat;
}
.mh-picker .mh-picker__indicator {
    width: 100%;
    height: 54px;
    position: absolute;
    left: 0;
    top: 108px;
    z-index: 3;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#d0d0d0), color-stop(#d0d0d0), color-stop(transparent), to(transparent)), -webkit-gradient(linear, left bottom, left top, from(#d0d0d0), color-stop(#d0d0d0), color-stop(transparent), to(transparent));
    background-image: linear-gradient(to bottom, #d0d0d0, #d0d0d0, transparent, transparent), linear-gradient(to top, #d0d0d0, #d0d0d0, transparent, transparent);
    background-position: top, bottom;
    background-size: 100% 1px;
    background-size: 100% 1px;
    background-repeat: no-repeat;
}
.mh-picker .mh-picker__content {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
}
.mh-picker__content__item {
  text-align: center;
  font-size: 18px;
  height: 54px;
  overflow: hidden;
  line-height: 54px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.mh-picker_item_active {
  color: #333;
  font-weight: 400;
}
.hastransition {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
